import React from 'react'
import { Card } from 'react-bootstrap'
import './shop.css'
function App({Item}){
    return(
        <>
         <Card border='' fluid style={{backgroundColor:'#16161d',color:'white',border:'none',width:'60%'}} >
    <Card.Img variant="top" className='cart-img'style={{width:'400px',height:'200px',objectFit:'contain',paddingleft:'20px'}}   src={Item.url} />
    <Card.Body>
      <Card.Title style={{color:'purple'}}>{Item.title}</Card.Title>
      <Card.Text>
      {Item.description}
      <br/>
     {Item.price}
     RS x
     {Item.qty}     
      </Card.Text>
    </Card.Body>
  </Card>
  <br/>
        </>
    )
}
export default App;