import "bootstrap/dist/css/bootstrap.min.css";
import {Link} from 'react-router-dom'
import './shop.css'
import ShoppingCart from '../shopping-cart.svg'
import {Link as ScrollLink} from 'react-scroll'
import {
  Navbar,
  Nav,
  Image,
  Container,
  Row,
  Col,
} from "react-bootstrap";
import cart from './cart.png'
function App(){
return(

<Navbar collapseOnSelect expand="lg" variant="dark" style={{ backgroundColor: "black" }} sticky="top">
 
<Navbar.Brand 
      className='navBrand justify-content-center' >    
          <Link to='/rage-coffe' ><Image   style={{height:'40px',width:'auto'}} src={`https://cdn.shopify.com/s/files/1/0062/9587/9780/files/Logo_Final_1coffeewhite_copy_x100.png?v=1596805614`}/></Link>
        </Navbar.Brand>
        
<Navbar.Toggle  aria-controls="responsive-navbar-nav" />

  <Navbar.Collapse  >
<Nav >
<Nav.Link  style={{textAlign:'center'}}><Link to='/rage-coffe' className='home-link' style={{ color: "grey",fontFamily: "Abril Fatface , cursive",textDecoration:'none'}}>Home</Link></Nav.Link> 

<Nav.Link  style={{textAlign:'center'}} >
  <ScrollLink to='WhyRage'   
        spy={true} 
        smooth={true} 
        duration={200} 
        style={{ color: "grey",fontFamily: "Abril Fatface , cursive",textDecoration:'none'}}>Why Rage?</ScrollLink></Nav.Link>
          <Nav.Link  style={{textAlign:'center'}} > <Link to='/rage-coffe/shop'  style={{ color: "white",fontFamily: "Abril Fatface , cursive",textDecoration:'none'}}>Shop</Link></Nav.Link>
          <Nav.Link  style={{textAlign:'center'}} > <Link to='/rage-coffe/checkout'  style={{ color: "grey",fontFamily: "Abril Fatface , cursive",textDecoration:'none'}}>Cart</Link></Nav.Link>

          <Nav.Link  style={{textAlign:'center'}} >
            <ScrollLink to='Science'   
        spy={true} 
        smooth={true} 
        duration={200} 
        style={{ color: "grey",fontFamily: "Abril Fatface , cursive",textDecoration:'none'}}>Science</ScrollLink></Nav.Link>
        </Nav> 

        </Navbar.Collapse>    
        <Navbar.Brand 
      className='justify-content-center' >    
          <Link to='/rage-coffe/checkout'className='cart-img' ><Image   style={{height:'40px',width:'auto',marginLeft:'45%',paddingBottom:'10px'}} src={cart}/></Link>
        </Navbar.Brand> 
      </Navbar>
      )
            }
      export default App