import React,{useState,useEffect} from 'react'
import Cart from './cart1'
import { Container,Row,Card,Image,Button,Col,Navbar,Nav } from 'react-bootstrap'
import {useSelector} from 'react-redux'

import {Link} from 'react-router-dom'
import './shop.css'
import ShoppingCart from '../shopping-cart.svg'
import {Link as ScrollLink} from 'react-scroll'
import cart from './cart.png'
import './shop.css'
function App(){
 const cart = useSelector(state => state.cart)
 const [totalPrice,SetTotalPrice]=useState(0);
 useEffect(()=>{let price=0;
cart.forEach(item=>price += item.qty*item.price)
SetTotalPrice(price)
},[totalPrice,SetTotalPrice,cart])
return(
    <>
    <Navbar collapseOnSelect expand="lg" variant="dark" style={{ backgroundColor: "black" }} sticky="top">
<Navbar.Brand 
      className='navBrand'  >
        
          <Link to='/ragecoffe' ><Image  style={{height:'40px',width:'auto'}} src={`https://cdn.shopify.com/s/files/1/0062/9587/9780/files/Logo_Final_1coffeewhite_copy_x100.png?v=1596805614`}/></Link>
        </Navbar.Brand>
        <Nav>
        <Nav.Link style={{textAlign:'center'}}><Link to='/rage-coffe' className='home-link' style={{ color: "grey",fontFamily: "Abril Fatface , cursive",textDecoration:'none'}}>Home</Link></Nav.Link>
          <Nav.Link  style={{textAlign:'center'}} ><Link to='/rage-coffe/shop'  style={{ color: "grey", fontFamily: "Abril Fatface, cursive",textDecoration:'none'}}>Shop</Link></Nav.Link>
        </Nav> 
      </Navbar>
    <Container fluid  >
      <Row>
      <Col lg={6} md={6}>
        
      {cart.map(prod=><Cart key={prod.id} {...prod} Item={prod } />)}
      </Col>
      <Col>
      <Card border='' className='payCard' fluid style={{backgroundColor:'#16161d',color:'white',border:'none',width:'60%'}}>
  <Card.Body>
    <Card.Title>Payment</Card.Title>
    <Card.Text>
  {totalPrice}Rs
    </Card.Text>
    <Button style={{marginTop:'10px',textTransform:'uppercase',backgroundColor:'black',border:'1px solid purple',borderRadius:'0',color:'purple'}} >PROCEED TO CHECKOUT</Button>{' '}
  </Card.Body>
</Card>
      </Col>
      </Row>
</Container>
</>
)
}
export default App;